<!-- Item.vue -->

<template>
  <li class="item">
    <div class="user-box">
      <div class="avatar" :style="`background-image: url(${user.photoURL})`">
      </div>
      <p class="user-name">{{ whisper.name }}</p>
    </div>
    <div v-if="editing" class="editor">
      <textarea 
        v-model="whisper.content" 
        placeholder="edit whisper" 
        @keypress.enter="updateWhisper">
      </textarea>
      <p class="message">Press Enter to Whisper</p>
    </div>
    <div v-else class="content" v-html="whisper.content"></div>
    <button v-if="currentUser && currentUser.uid == user.uid" @click="showBtns = !showBtns">
      <fa icon="ellipsis-v" />
    </button>
    <div v-if="showBtns" class="controls">
      <li @click="editing = !editing">edit</li>
      <li @click="deleteWhisper" style="color: red">
        delete
      </li>
    </div>
  </li>
</template>

<script>
import { db } from '../main'
import {   
  collection,
  documentId,
  getDocs,
  query,
  doc,
  deleteDoc,
  where,
  updateDoc,
  onSnapshot,
  setDoc } from "firebase/firestore";
import { getAuth } from '@firebase/auth';


export default {
  props: ['id','uid'],
  data () {
    return {
      whisper:{},
      user: [],
      currentUser: {},
      showBtns: false,
      editing: false 
    }
  },
  created () {
    const auth = getAuth()
    auth.onAuthStateChanged(user => {
      this.currentUser = user
    })
  },
  methods:{
    async deleteWhisper(){
      if (window.confirm('Are You Sure to Delete This Whisper?')) {
        const whisperRef = doc(db, 'whispers', this.id);
        await deleteDoc(whisperRef);
      }
    },
    // ドキュメント全体の更新：setDoc
    // ドキュメントの一部の更新：updateDoc
    async updateWhisper () {
      const whisperRef = doc(db, "whispers", this.id);
      const date = new Date()
      const data = {
        'content': this.whisper.content,
        'date': date,
      }
      await updateDoc(whisperRef,data).then(
          this.editing = false
        );
    },
  },
    mounted: async function(){
      // getDocsがFirebaseとの通信となるため、awaitで同期
      const whispersRef  = collection(db, "whispers");
      await getDocs(query(whispersRef, where(documentId(), "==", this.id))).then(snapshot => {
        snapshot.forEach(doc => {
          this.whisper = doc.data()
        })
    })


    const userRef  = collection(db, "users");
    await getDocs(query(userRef, where("uid", "==", this.uid))).then(snapshot => {
      snapshot.forEach(doc => {
        this.user = doc.data()
      })
    })
  }

  
}
</script>

<style lang="stylus">
.item
  list-style none
  border-top 1px solid #eee
  padding 5px 15px
  display flex
  flex-wrap no-wrap
  justify-content flex-start
  position relative
  &:first-child
    border none
  &:hover
    background rgba(0,0,0,.02)
  .user-box
    margin 10px 10px 10px 0
    .avatar
      height 50px
      width 50px
      border-radius 50%
      border 1px solid #eee
      background-size cover
    .user-name
      margin 5px 0 0 0
      text-align center
      font-size .7rem
      line-height .7rem
      width 50px
  .content
    padding 10px
  .editor
    position relative
    width 100%
    textarea
      background transparent
      resize none
      height 80px
      width 96%
      border none
      padding 10px 2%
      font-size .9rem
      font-weight lighter
      &:focus
        outline none
    .message
      opacity 0  
      position absolute
      bottom 5px
      right 10px
      transition .2s
      font-size .8rem
    &:hover
      .message
        opacity 1
  button
    position absolute
    top 5px
    right 0
    background transparent
    color #555
    font-size .9rem
    opacity 0
    transition .2s
  .controls
    background white
    position absolute
    top 5px
    right 35px
    box-shadow 0 0 5px rgba(0,0,0,.05)
    border-radius 3px
    opacity 0
    li
      padding 5px 20px
      border-top 1px solid #eee
      cursor pointer
      &:first-child
        border none
  &:first-child
    border none
  &:hover
    background rgba(0,0,0,.02)
    .content
    button
      opacity 1
    .controls
      opacity 1
</style>
